
import ThumsUpDialog from '@/components/front/thums-up-dialog.vue';
import { defineComponent, ref, Ref, onMounted } from 'vue';
import { getBlogDetail } from '@/api/blog';
import { BlogDetailResponse } from '@/request/blog';
import { HttpResponse } from '@/request/response';
import { useRoute } from 'vue-router';
import viewer from '@/components/front/markdown-viewer.vue';
import ArticleAd from '@/components/front/article-ad.vue';
import { sleep } from '@/utils/common';

export default defineComponent({
  name: 'DetailPage',
  components: {
    ThumsUpDialog,
    viewer,
    ArticleAd
  },
  setup(){
    let thumsUpDialogVisible: Ref<boolean> = ref(false);
    /****** 查询博客内容 ******/
    const route = useRoute();
    let blogDetail: Ref<BlogDetailResponse> = ref(new BlogDetailResponse());
    const getBlogDetailSuccess = (httpResponse: HttpResponse<BlogDetailResponse>) => {
      blogDetail.value = httpResponse.data;
      (document.querySelector('title') as any).innerText = blogDetail.value.title;
      (document.querySelector('meta[name="keywords"]') as any).setAttribute('content', blogDetail.value.type);
      (document.querySelector('meta[name="description"]') as any).setAttribute('content', blogDetail.value.title);
    }
    const getBlog = async () => {
      let blogId: string = <string>(route?.params?.id ?? '');
      let httpResponse: HttpResponse<BlogDetailResponse> = await getBlogDetail(blogId);
      await httpResponse.handleResponseData(getBlogDetailSuccess, httpResponse.defaultFailHandler);
      await sleep(1000);
      document.dispatchEvent(new Event('render-event'));
      rewriteImageUrl();
    }
    const rewriteImageUrl = () => {
      let imageElementList = document.querySelectorAll('img');
      if (imageElementList.length > 0){
        imageElementList.forEach(img => {
          let startIndex: number = img.src.indexOf('/apiupload/');
          if (startIndex !== -1){
            let src = `${process.env.VUE_APP_API_URL}${img.src.substr(startIndex)}`;
            img.src = src;
          }
        })
      }
    }
    onMounted(() => {
      getBlog();
    })
    return {
      thumsUpDialogVisible,
      blogDetail
    }
  }
})
