import "core-js/modules/web.timers.js";
export default {
  name: "ArticleAd",
  data: function data() {
    return {};
  },
  mounted: function mounted() {// this.initAdHtml();
  },
  methods: {
    initAdHtml: function initAdHtml() {
      var element = this.$refs['ad-container']; // 创建 script 标签

      var scriptElement = document.createElement('script'); // script1.setAttribute('async');

      scriptElement.setAttribute('src', 'https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-3783940895852487');
      scriptElement.setAttribute('crossorigin', 'anonymous');
      element.appendChild(scriptElement); // 创建 ins 标签

      var insElement = document.createElement('ins');
      insElement.classList.add('adsbygoogle');
      insElement.style.display = 'block';
      insElement.style.textAlign = 'center';
      insElement.dataset['adLayout'] = 'in-article';
      insElement.dataset['adFormat'] = 'fluid';
      insElement.dataset['adClient'] = 'ca-pub-3783940895852487';
      insElement.dataset['adSlot'] = '6829800567';
      element.appendChild(insElement); // ins 标签创建完成

      var intervalId = setInterval(function () {
        if (window.adsbygoogle) {
          (adsbygoogle = window.adsbygoogle || []).push({});
          clearInterval(intervalId);
        }
      }, 500);
    }
  }
};